* {
    box-sizing: border-box;
}
body {
    width: 320px;
    margin: 0 auto;
    margin-top: 30px;
    padding: 0;
}
h1,
h2,
h3 {
    font-weight: normal;
    margin: 0;
}
@media (min-width: $tablet-width) { 
    body {
        width: 100%;
        max-width: 768px;
        margin: 0 auto;
        margin-top: 22px;
        padding: 0;
    }
}